import {
    UniformsLib,
    Vector4,
    Vector3,
    Vector2
} from 'three';

// import { RenderPass, NormalPass, SSAOMaterial } from 'postprocessing'
import { shaderMaterial } from '@react-three/drei'

import { mergeUniforms } from 'three/src/renderers/shaders/UniformsUtils'
  
// https://stackoverflow.com/questions/54080336/how-do-i-import-code-from-three-js-examples
const DepthMaterialUniforms = {
  constant_buffer_0: {
    cb0_16xyz: { value: new Vector3( 0.286179, 1.272233, 3.5616 ) },
    cb0_21w: { value: 0.0 }, // is perspective camera (=0) or orthographic camera (=1)
    cb0_138yz: { value: new Vector2( 1, 1 ) },
    cb0_137x: { value: 0.00012 }, 
    cb0_139xyz: { value: new Vector3( 0.11008, 0.51657, 0.84914 ) },
    cb0_67: { value: new Vector4( 0.10962, 0.51708, 0.84889, 0.00 ) },

	},
	constant_buffer_1: {
    cb1_9w: { value: 1.0 }, // cross(normal, tangent)'s direction
  },
  constant_buffer_2: {
    cb2_0x: { value: 1.0 },
    cb2_186y: { value: 0.0 },
    cb2_190x: { value: 0.0 },
    cb2_194w: { value: 1.0 },
    cb2_139xy: { value: new Vector2( 0, 0 ) },
    cb2_142x: { value: 1.0 }, 
    cb2_183: { value: new Vector3( 0.16652, -0.00164, 0.98604 ) },
    cb2_7x: { value: 0.0 }, 
    cb2_6w: { value: 1.0 }, 
    cb2_8: { value: new Vector4( 0, 0, 1, 0 ) },
    cb2_4w: { value: 1.0 }, 
    cb2_3w: { value: 1.0 }, 
    cb2_9x: { value: 0.5 }, 
  },
}

const uniform_prefix = String(`
uniform float cb2_0x;
uniform float cb2_186y;
uniform float cb2_190x;
uniform float cb2_194w;
uniform float cb1_9w; // cross(normal, tangent)'s direction
uniform vec3 cb0_16xyz;
uniform float cb0_21w; // is perspective camera (=0) or orthographic camera (=1)
uniform vec2 cb2_139xy;
uniform vec2 cb0_138yz;
uniform float cb0_137x;
uniform float cb2_142x;
uniform vec3 cb0_139xyz;
uniform vec4 cb0_67;
uniform vec3 cb2_183;
uniform float cb2_7x;
uniform float cb2_6w;
uniform vec4 cb2_8;
uniform float cb2_4w;
uniform float cb2_3w;
uniform float cb2_9x;
`); 
// class MeshNormalMaterial extends Material
// its defined PARAMETERS will be inspected by WebGLProgram and assign corresponding defines or uniform
// If we don't use the standard material, we just have to define defines or parameters in uniform argument, see Splat.js SplatMaterial
// It use alphaTest as uniform and it use alphaTest define in fragment shader
function convertUniformFormat(rawUniforms) {
  const dst = {};
  for (const u in rawUniforms) {
    dst[u] = rawUniforms[u].value;
    // console.log(u, rawUniforms[u].value);
  }
  return dst;
}
// node_modules/three/src/renderers/shaders/ShaderLib/meshnormal.glsl.js
// FIXME Depth-only pass is for shadowMap generation
const CustomMeshDepthMaterial = shaderMaterial(
  convertUniformFormat(
    mergeUniforms( [
      UniformsLib.common,
      UniformsLib.bumpmap,
      UniformsLib.normalmap,
      UniformsLib.displacementmap,
      {
        opacity: { value: 1.0 },
        vertexTangents: { value: true },
        vertexUv1s: { value: true }, // enable attribute uv1 accept, attribute uv is already provided
        vertexUv2s: { value: true }, // enable attribute uv2 accept
        vertexUv3s: { value: true }, // enable attribute uv3 accept
        vertexAlphas: {value: true }, // For vertex color+alpha attribute

        diffMap: { value: null },
        baseTexture: { value: null },
      },
      DepthMaterialUniforms.constant_buffer_0,
      DepthMaterialUniforms.constant_buffer_1,
      DepthMaterialUniforms.constant_buffer_2,
    ] )),
    /* glsl */uniform_prefix+`
    #define NORMAL
  
    //in vec3 position; // The commented input attribute is defined and don't need to define here
    //in vec3 normal;
    #ifndef USE_TANGENT
    in vec4 tangent;
    #endif
    in vec4 color;

    //in vec2 uv;
    in vec2 uv1;
    in vec2 uv2;
    in vec2 uv3;

    // It actually correspond to v7 (NormalTS)
    in vec2 texcoord_4;
    in vec2 texcoord_5;


    out vec3 outputVertexToCameraDirection;
    out vec4 vNormal;
    out vec4 vPosition;
    out vec4 vColor; // corresponding to o7
    out vec4 vColor1; // corresponding to o4

    out vec4 vUV01;
    out vec4 vUV23;
  
    // #include <skinning_pars_vertex>

    // ----- CUSTOM ------
    // --------------------
    
    void main() {
      if (cb2_0x == 0.0 || cb2_186y == 1.0 || cb2_190x == 1.0 || cb2_194w == 0.0) {
        // Set all output attribute to zero
        return;
      }

      vec4 uv_scale_offset = vec4(1.0, 1.0, 0.0, 0.0); // cb2_1
      vec2 output_uv_0 = uv * uv_scale_offset.xy + uv_scale_offset.zw;
      vec2 output_uv_1 = uv1 * uv_scale_offset.xy + uv_scale_offset.zw;
      vec2 output_uv_2 = uv2 * uv_scale_offset.xy + uv_scale_offset.zw;
      vec2 output_uv_3 = uv3 * uv_scale_offset.xy + uv_scale_offset.zw;

      // position: vec3
      // modelMatrix presented at WebGLRenderer.js? modelMatrix: mat4
      // modelMatrix = cb1_0123
      vec3 transformed_position = vec3(modelMatrix * vec4(position, 1.0));

      // normalMatrix = transposed_modelMatrix = transpose(cb1_012)
      // normal defined in WebGLProgram
      vec3 transformedNormal = normalMatrix * normal;
      transformedNormal = normalize( transformedNormal );

      float some_value_3;
      if (cb1_9w >= 0.0) {
        some_value_3 = 1.0;
      } else {
        some_value_3 = -1.0;
      }
        // if you want to define tangent, just add vertexTangents into parameter and WebGLProgram will do it for you 

      some_value_3 = tangent.w * some_value_3;
      vec3 transformedTangent = mat3(modelMatrix) * vec3(tangent);
      transformedTangent = normalize(transformedTangent);

      //(a.y*b.z-a.z*b.y, a.z*b.x-a.x*b.z, a.x*b.y-a.y*b.x)
      vec3 transformedBitangent = cross(transformedNormal, transformedTangent);
      transformedBitangent = some_value_3 * transformedBitangent;

      // outputVertexToCameraDirection = o6
      if (cb0_21w == 0.0) {
        // Perspective camera: camera to vertex position direction
        outputVertexToCameraDirection = cb0_16xyz - transformed_position;
      } else {
        // Orthographic camera: Eye look into direction (View matrix's Z direction)
        outputVertexToCameraDirection = vec3(viewMatrix[0].z, viewMatrix[1].z, viewMatrix[2].z); // get viewMatrix's z direction vector
      }
      
      // added start --- added end
      vec2 some_vector_10 = vec2(3.5, -3.5) * cb0_137x * (cb2_139xy + cb0_138yz);

      vec4 normalTS = vec4(texcoord_4, texcoord_5);
      vec3 normalWS = normalTS.z * transformedNormal + normalTS.x * transformedTangent + normalTS.y * transformedBitangent;
      if (cb2_142x != 0.0) {
        if (normalTS.z != 0.0) {
        } else {
          normalWS = transformedNormal;
        }
      } else {
        normalWS = transformedNormal;
      }

      float some_value_11 = 1.0 - clamp(dot(-cb0_139xyz, normalWS), 0.0, 1.0);
      some_value_11 *= some_vector_10.y;

      vec3 some_vector_12 = transformed_position - some_vector_10.x * cb0_139xyz + some_value_11 * normalWS;
      // in original shader, viewProjectionMatrix is together, but in three.js, it's separate, viewProjectionMatrix = cb0_73_to_76
      // TODO the projectionMatrix should change to orthogonal matrix
      // TODO use fake projeciton matrix currently
      mat4 fakeProjectionMatrix = mat4(4.27263696558, 0.0, 0.0, 0.0, // zoom in factor
      0.0, -7.59577978336, 0.0, 0.0, // zoom in factor * aspect ratio
      0.0, 0.0, -0.0003, -1.0, // use mine, it will become -1.0006...
      0.0, 0.0, -0.30009, 0.0); // use mine, it will become -0.6002...
      gl_Position = fakeProjectionMatrix * viewMatrix * vec4(some_vector_12, 1.0);

      // gl_Position = projectionMatrix * viewMatrix * vec4(some_vector_12, 1.0);
      gl_Position.z = min(gl_Position.z, gl_Position.w);

      vPosition = vec4(transformed_position, 0.0);

      vColor1 = vec4(0.0);

      vNormal = vec4(transformedNormal, 0.0);

      vColor = color;



      // #include <skinbase_vertex>
      // #include <skinnormal_vertex>
    }
    `,
    /* glsl */uniform_prefix+`
    #define NORMAL
  
    // #define USE_BASE_MAP // body use 2_fs
  
    in vec3 outputVertexToCameraDirection;
    in vec4 vNormal;
    in vec4 vPosition;
    in vec4 vColor; // corresponding to o7
    in vec4 vColor1; // corresponding to o4

    in vec4 vUV01;
    in vec4 vUV23;


    uniform float opacity;    
    
    // ---------CUSTOM --------
    #ifdef USE_BASE_MAP
    uniform sampler2D baseTexture;
    uniform sampler2D diffMap;

    #endif
    // -------------------------

    void main() {
      // ------- CUSTOM --------
      // TODO 1_fs not defined it, 2_fs defined USE_BASE_MAP, it's corresponding to the first color pass
      #ifdef USE_BASE_MAP
      // mat4 cb1_4567 = mat4(vec4(0.8105, -0.19902, 0.55089, 0.00), vec4(0.17636, 0.97978, 0.09449, 0.00), vec4(-0.55856, 0.02057, 0.82921, 0.00), vec4(-0.16085, -0.86985, -0.0878, 1.00));
      mat4 modelMatrix1 = mat4(0.1000, 0, 0, 0, 0, 0.0000, 0.1000, 0, 0, -0.1000, 0.0000, 0, 0.0011, 0.1778, 0.0002, 1);
      mat3 transposedModelMatrix = transpose(mat3(modelMatrix1));
      mat4 cb1_4567 = mat4(transposedModelMatrix);
      vec3 inverseTranslation = -transposedModelMatrix * vec3(modelMatrix1[0].w, modelMatrix1[1].w, modelMatrix1[2].w);
      cb1_4567[0].w = inverseTranslation.x;
      cb1_4567[1].w = inverseTranslation.y;
      cb1_4567[2].w = inverseTranslation.z;
      cb1_4567[3].w = 1.0;      
      // mat3 cb1_012 = mat3(vec3(0.8105, 0.17636, -0.55856), vec3(-0.19902, 0.97978, 0.02057), vec3(0.55089, 0.09449, 0.82921));
      mat3 cb1_012 = mat3(modelMatrix1);
      vec3 some_vec4 = (cb1_4567 * cb0_67).xyz;
      some_vec4 = cb1_012 * some_vec4;

      float some_float = clamp(dot(cb2_183.xyz, some_vec4), 0.0, 1.0);
      some_float = pow(some_float, 4.0) + -1.0;

      some_float = cb2_6w * (some_float * cb2_7x + 1.0);
      if (cb2_7x <= 0.0) {
        some_float = cb2_6w;
      }
      vec4 diff_value = texture(diffMap, vUV01.xy);
      vec4 base_value = texture(baseTexture, vUV01.xy);

      float is_specific_part = dot(base_value, cb2_8);

      float some_float_2 = some_float * is_specific_part + (1.0 - some_float) * diff_value.a;

      // TODO: provide both maps to shader
      if (some_float_2 * (cb2_4w * cb2_3w) - cb2_9x < 0.0) {
        discard;
      }

      float some_value_3 = some_float * is_specific_part + (1.0 - some_float) * diff_value.a;
      if (some_value_3 * (cb2_4w * cb2_3w) - cb2_9x < 0.0) {
        discard;
      }
      #endif
      // End here
      gl_FragColor = vec4( 1.0 );
      // ------- CUSTOM END --------
    
    }
    `,
  );

export{ CustomMeshDepthMaterial }