export class FlipScreenMaterial extends THREE.ShaderMaterial {
	constructor() {
	  super({
  
	  name: 'FlipScreenMaterial',
  
	  uniforms: {
  
		'inputBuffer': { value: null },
		'opacity': { value: 1.0 }
  
	  },
  
	  vertexShader: /* glsl */`
  
		varying vec2 vUv;
  
		void main() {
  
		  vUv = uv;
		  gl_Position = projectionMatrix * modelViewMatrix * vec4( vec3(position.x, position.y, position.z), 1.0 );
		  gl_Position.y = -gl_Position.y;
		  gl_Position.x = gl_Position.x;
  
		}`,
  
	  fragmentShader: /* glsl */`
  
		uniform float opacity;
  
		uniform sampler2D inputBuffer;
  
		varying vec2 vUv;
  
		void main() {
  
		  vec4 texel = texture2D( inputBuffer, vUv );
		  gl_FragColor = vec4(texel.rgb, 1.0);
  
  
		}`,
		blending: THREE.NoBlending,
		toneMapped: false,
		depthWrite: false,
		depthTest: false,
		side: THREE.BackSide
	  });
	}
	  /**
	   * The input buffer.
	   *
	   * @type {Texture}
		 */
	  set inputBuffer(value) {
		this.uniforms.inputBuffer.value = value;
	  }
	  /**
	   * Sets the input buffer.
	   *
	   * @deprecated Use inputBuffer instead.
	   * @param {Number} value - The buffer.
	   */
	  setInputBuffer(value) {
		this.uniforms.inputBuffer.value = value;
	  }
}

export class FlipScreenPass extends ShaderPass {
	constructor(screenTexture) {
		super(new FlipScreenMaterial());
		this.renderToScreen = true;
	}

	// Since original ShaderPass will directly reassign inputBuffer to the argument inputBuffer from effectComposer, we need to take it off in this stage
	render(renderer, inputBuffer, outputBuffer, deltaTime, stencilTest) {
		const uniforms = this.fullscreenMaterial.uniforms;
		uniforms[this.input].value = inputBuffer.texture;
		// console.log(uniforms[this.input].value);
		renderer.setRenderTarget(null);
		renderer.render(this.scene, this.camera);
	}
}
export class DebugSSAOMaterial extends THREE.ShaderMaterial {
  constructor() {
    super({

    name: 'DebugSSAOMaterial',

    uniforms: {

      'inputBuffer': { value: null },
      'opacity': { value: 1.0 }

    },

    vertexShader: /* glsl */`

      varying vec2 vUv;

      void main() {

        vUv = uv;
        gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );

      }`,

    fragmentShader: /* glsl */`

      uniform float opacity;

      uniform sampler2D inputBuffer;

      varying vec2 vUv;

      void main() {

        vec4 texel = texture2D( inputBuffer, vUv );
  			gl_FragColor =  vec4(texel.r, texel.g, texel.b, 1.0);


      }`,
      blending: THREE.NoBlending,
      toneMapped: false,
      depthWrite: false,
      depthTest: false,

    });
  }
	/**
	 * The input buffer.
	 *
	 * @type {Texture}
   	*/
    set inputBuffer(value) {
      this.uniforms.inputBuffer.value = value;
    }
    /**
     * Sets the input buffer.
     *
     * @deprecated Use inputBuffer instead.
     * @param {Number} value - The buffer.
     */
    setInputBuffer(value) {
      this.uniforms.inputBuffer.value = value;
    }
}

export class DebugSSAOPass extends ShaderPass {
	constructor(ssaoTexture) {
		super(new DebugSSAOMaterial());
		const uniforms = this.fullscreenMaterial.uniforms;
		uniforms[this.input].value = ssaoTexture;
	}

	// Since original ShaderPass will directly reassign inputBuffer to the argument inputBuffer from effectComposer, we need to take it off in this stage
	render(renderer, inputBuffer, outputBuffer, deltaTime, stencilTest) {
		renderer.setRenderTarget(this.renderToScreen ? null : outputBuffer);
		renderer.render(this.scene, this.camera);
	}
}
