export class DebugDepthMaterial extends THREE.ShaderMaterial {
	constructor() {
	  super({
  
	  name: 'DebugDepthMaterial',
  
	  uniforms: {
  
		'inputBuffer': { value: null },
		'cameraNear': { value: 0.1 },
		'cameraFar': { value: 1000.0 },
		'opacity': { value: 1.0 }
  
	  },

	  defines: {
        PERSPECTIVE_CAMERA: "1"
      },
  
	  vertexShader: /* glsl */`
  
		varying vec2 vUv;
  
		void main() {
  
		  vUv = uv;
		  gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
  
		}`,
  
	  fragmentShader: /* glsl */`
  
		uniform float opacity;
  
		uniform mediump sampler2D inputBuffer;

		uniform float cameraNear;
		uniform float cameraFar;

		varying vec2 vUv;
		
		float viewZToOrthographicDepth( const in float viewZ, const in float near, const in float far ) {
			return ( viewZ + near ) / ( near - far );
		}
		float perspectiveDepthToViewZ( const in float depth, const in float near, const in float far ) {
			return ( near * far ) / ( ( far - near ) * depth - far );
		}
  
		void main() {
  
		  float depth = texture2D( inputBuffer, vUv ).r;

		  #ifdef PERSPECTIVE_CAMERA
			  float viewZ = perspectiveDepthToViewZ(depth, cameraNear, cameraFar);
			  float linearDepth = viewZToOrthographicDepth(viewZ, cameraNear, cameraFar);
		  #else
			  float linearDepth = depth;
		  #endif
		  float scaledDepth = clamp(1.0-linearDepth * 10.0, 0.0, 1.0);
		  gl_FragColor = vec4(scaledDepth, scaledDepth, scaledDepth, 1.0);
  
		}`,
		blending: THREE.NoBlending,
		toneMapped: false,
		depthWrite: false,
		depthTest: false,
  
	  });
	}
	  /**
	   * The input buffer.
	   *
	   * @type {Texture}
		 */
	  set inputBuffer(value) {
		this.uniforms.inputBuffer.value = value;
	  }
	  /**
	   * Sets the input buffer.
	   *
	   * @deprecated Use inputBuffer instead.
	   * @param {Number} value - The buffer.
	   */
	  setInputBuffer(value) {
		this.uniforms.inputBuffer.value = value;
	  }
  }
  
  export class DebugDepthPass extends ShaderPass {
	  constructor(depthTexture) {
		  super(new DebugDepthMaterial());
		  const uniforms = this.fullscreenMaterial.uniforms;
		  uniforms[this.input].value = depthTexture;
	  }
  
	  // Since original ShaderPass will directly reassign inputBuffer to the argument inputBuffer from effectComposer, we need to take it off in this stage
	  render(renderer, inputBuffer, outputBuffer, deltaTime, stencilTest) {
		  renderer.setRenderTarget(this.renderToScreen ? null : outputBuffer);
		  renderer.render(this.scene, this.camera);
	  }
  }
