export class CustomNormalPass extends Pass {

	/**
	 * Constructs a new normal pass.
	 *
	 * @param {Scene} scene - The scene to render.
	 * @param {Camera} camera - The camera to use to render the scene.
	 * @param {Object} [options] - The options.
	 * @param {WebGLRenderTarget} [options.renderTarget] - A custom render target.
	 * @param {Number} [options.resolutionScale=1.0] - The resolution scale.
	 * @param {Number} [options.resolutionX=Resolution.AUTO_SIZE] - The horizontal resolution.
	 * @param {Number} [options.resolutionY=Resolution.AUTO_SIZE] - The vertical resolution.
	 * @param {Number} [options.width=Resolution.AUTO_SIZE] - Deprecated. Use resolutionX instead.
	 * @param {Number} [options.height=Resolution.AUTO_SIZE] - Deprecated. Use resolutionY instead.
	 */

	constructor(scene, camera, {
		renderTarget,
		resolutionScale = 1.0,
		width = Resolution.AUTO_SIZE,
		height = Resolution.AUTO_SIZE,
		resolutionX = width,
		resolutionY = height
	} = {}) {
		super("NormalPass");

		this.needsSwap = false;

		/**
		 * A render pass.
		 *
		 * @type {RenderPass}
		 * @private
		 */

		// NOTE: we can pass parameters such as uniforms, defines into this material
		// TODO: define necessary field for individual mesh material
		// RenderPass only use OverrideMaterialManager to change every mesh to render using the overridedMaterial
		// See OverrideMaterialManager's replaceMaterial in R3F's index.cjs
		// TODO: Before rendering, we should use the similar trick used by the OverrideMaterialManager
		// That is changing scene.overrideMaterial before renderer.render() to manually override each mesh's material
		// A little bit like SSRPass.js mode, but my implementation will be constructing a characteristic structure containing mesh specific defines and attribute and traverse each node to render the same material with mesh-specific characteristic
		// https://github.com/pmndrs/three-stdlib/blob/8e15117f8252a77cf1c0b12ce854919182f7c5ff/src/webxr/XRControllerModelFactory.ts#L10
		// this.renderPass = new RenderPass(scene, camera, new CustomMeshNormalMaterial({
		// 	defines: {
		// 		TEST_DEFINE: 1
		// 	}
		// })); // MeshNormalMaterial
		this.renderPass = new RenderPass(scene, camera); // MeshNormalMaterial
		// this.renderPass = new RenderPass(scene, camera, new MeshNormalMaterial()); // MeshNormalMaterial

		const renderPass = this.renderPass;
		renderPass.ignoreBackground = true;
		renderPass.skipShadowMapUpdate = true;

		const clearPass = renderPass.getClearPass();
		clearPass.overrideClearColor = new Color(0x7777ff);
		clearPass.overrideClearAlpha = 1.0;

		/**
		 * A render target for the scene normals.
		 *
		 * @type {WebGLRenderTarget}
		 * @readonly
		 */

		this.renderTarget = renderTarget;

		if(this.renderTarget === undefined) {

			this.renderTarget = new WebGLRenderTarget(1, 1, {
				minFilter: NearestFilter,
				magFilter: NearestFilter,
				depthBuffer: true,
				depthTexture: new DepthTexture(1, 1)
			});

			this.renderTarget.texture.name = "NormalPass.Target";

		}

		/**
		 * The resolution.
		 *
		 * @type {Resolution}
		 * @readonly
		 */

		const resolution = this.resolution = new Resolution(this, resolutionX, resolutionY, resolutionScale);
		resolution.addEventListener("change", (e) => this.setSize(resolution.baseWidth, resolution.baseHeight));

	}

	set mainScene(value) {

		this.renderPass.mainScene = value;

	}

	set mainCamera(value) {

		this.renderPass.mainCamera = value;

	}

	/**
	 * The normal texture.
	 *
	 * @type {Texture}
	 */

	get texture() {

		return this.renderTarget.texture;

	}

	/**
	 * The normal texture.
	 *
	 * @deprecated Use texture instead.
	 * @return {Texture} The texture.
	 */

	getTexture() {

		return this.renderTarget.texture;

	}

	/**
	 * Returns the resolution settings.
	 *
	 * @deprecated Use resolution instead.
	 * @return {Resolution} The resolution.
	 */

	getResolution() {

		return this.resolution;

	}

	/**
	 * Returns the current resolution scale.
	 *
	 * @return {Number} The resolution scale.
	 * @deprecated Use resolution.preferredWidth or resolution.preferredHeight instead.
	 */

	getResolutionScale() {

		return this.resolution.scale;

	}

	/**
	 * Sets the resolution scale.
	 *
	 * @param {Number} scale - The new resolution scale.
	 * @deprecated Use resolution.preferredWidth or resolution.preferredHeight instead.
	 */

	setResolutionScale(scale) {

		this.resolution.scale = scale;

	}

	/**
	 * Renders the scene normals.
	 *
	 * @param {WebGLRenderer} renderer - The renderer.
	 * @param {WebGLRenderTarget} inputBuffer - A frame buffer that contains the result of the previous pass.
	 * @param {WebGLRenderTarget} outputBuffer - A frame buffer that serves as the output render target unless this pass renders to screen.
	 * @param {Number} [deltaTime] - The time between the last frame and the current one in seconds.
	 * @param {Boolean} [stencilTest] - Indicates whether a stencil mask is active.
	 */

	render(renderer, inputBuffer, outputBuffer, deltaTime, stencilTest) {

		const renderTarget = this.renderToScreen ? null : this.renderTarget;

		this.renderPass.scene.traverseVisible((child) => {
			if (child.isMesh && child instanceof CustomMesh) {
				// Switch material before rendering
				child._CustomNormalBackupMaterial = child.material;
				child.material = child.customNormalMaterial;
			}
		  });	  
		this.renderPass.render(renderer, renderTarget, renderTarget);
		this.renderPass.scene.traverseVisible((child) => {
			if (child.isMesh) {
				child.material = child._CustomNormalBackupMaterial;
			}
		  });

	}

	/**
	 * Updates the size of this pass.
	 *
	 * @param {Number} width - The width.
	 * @param {Number} height - The height.
	 */

	setSize(width, height) {

		const resolution = this.resolution;
		resolution.setBaseSize(width, height);
		this.renderTarget.setSize(resolution.width, resolution.height);

	}

}

export class CustomSSAOPass extends Pass {

	/**
	 * Constructs a new normal pass.
	 *
	 * @param {Scene} scene - The scene to render.
	 * @param {Camera} camera - The camera to use to render the scene.
	 * @param {Object} [options] - The options.
	 * @param {WebGLRenderTarget} [options.renderTarget] - A custom render target.
	 * @param {Number} [options.resolutionScale=1.0] - The resolution scale.
	 * @param {Number} [options.resolutionX=Resolution.AUTO_SIZE] - The horizontal resolution.
	 * @param {Number} [options.resolutionY=Resolution.AUTO_SIZE] - The vertical resolution.
	 * @param {Number} [options.width=Resolution.AUTO_SIZE] - Deprecated. Use resolutionX instead.
	 * @param {Number} [options.height=Resolution.AUTO_SIZE] - Deprecated. Use resolutionY instead.
	 */

	constructor(camera, normalBuffer, {
		blendFunction = BlendFunction.MULTIPLY,
		samples = 31,
		rings = 7,
		depthBuffer = null,
		normalDepthBuffer = null,
		depthAwareUpsampling = true,
		worldDistanceThreshold,
		worldDistanceFalloff,
		worldProximityThreshold,
		worldProximityFalloff,
		distanceThreshold = 1.0,
		distanceFalloff = 0.0,
		rangeThreshold = 0.5,
		rangeFalloff = 0.1,
		minRadiusScale = 0.1,
		luminanceInfluence = 0.7,
		radius = 5,
		intensity = 100,
		bias = 0.5,
		fade = 0.01,
		color: color2 = null,
		renderTarget,
		resolutionScale = 1.0,
		width = Resolution.AUTO_SIZE,
		height = Resolution.AUTO_SIZE,
		resolutionX = width,
		resolutionY = height
	} = {}) {
		super("CustomSSAOPass");

		this.needsSwap = false;

		/**
		 * A render pass.
		 *
		 * @type {ShaderPass}
		 * @private
		 */

		this.depthDownsamplingPass = new DepthDownsamplingPass({ normalBuffer, resolutionScale });
		if (depthBuffer === null && normalDepthBuffer === null) {
			this.depthDownsamplingPass.enabled = true;	 
		}
		this.camera = camera;
		this.shaderPass = new ShaderPass(new SSAOMaterial(camera));

		const noiseTexture = new NoiseTexture(64, 64, THREE.RGBAFormat);
		noiseTexture.wrapS = noiseTexture.wrapT = THREE.RepeatWrapping;
		const ssaoMaterial = this.ssaoMaterial;
		// ssaoMaterial.noiseTexture = noiseTexture;
		// ssaoMaterial.blendFunction = BlendFunction.MULTIPLY; // Use NORMAL to see the effect
		// ssaoMaterial.samples = 31;
		// ssaoMaterial.radius = 5;
		// ssaoMaterial.intensity = 100;
		// ssaoMaterial.distanceFalloff = 0.0;
		// ssaoMaterial.distanceThreshold = 1.0;
		// ssaoMaterial.proximityFalloff = 0.1;
		// ssaoMaterial.proximityThreshold = 0.5;
		// ssaoMaterial.setBias(0.5);
		// ssaoMaterial.setMinRadiusScale(0.1);
		// ssaoMaterial.normalDepthBuffer = downsamplingPass.texture;
		ssaoMaterial.blendFunction = blendFunction; // Use NORMAL to see the effect
		ssaoMaterial.intensity = intensity;
		ssaoMaterial.normalBuffer = normalBuffer;
		ssaoMaterial.noiseTexture = noiseTexture;
		ssaoMaterial.setMinRadiusScale(minRadiusScale);
		ssaoMaterial.samples = samples;
		ssaoMaterial.radius = radius;
		ssaoMaterial.rings = rings;
		ssaoMaterial.fade = fade;
		ssaoMaterial.setBias(bias);
		ssaoMaterial.distanceFalloff = distanceFalloff;
		ssaoMaterial.distanceThreshold = distanceThreshold;
		ssaoMaterial.proximityFalloff = rangeFalloff;
		ssaoMaterial.proximityThreshold = rangeThreshold;
		if (worldDistanceThreshold !== void 0) {
		  ssaoMaterial.worldDistanceThreshold = worldDistanceThreshold;
		}
		if (worldDistanceFalloff !== void 0) {
		  ssaoMaterial.worldDistanceFalloff = worldDistanceFalloff;
		}
		if (worldProximityThreshold !== void 0) {
		  ssaoMaterial.worldProximityThreshold = worldProximityThreshold;
		}
		if (worldProximityFalloff !== void 0) {
		  ssaoMaterial.worldProximityFalloff = worldProximityFalloff;
		}
		this.depthTexture = null;
		if (normalDepthBuffer !== null) {
		  this.ssaoMaterial.normalDepthBuffer = normalDepthBuffer;
		} else if (depthBuffer !== null) {
		  this.depthTexture = depthBuffer;
		  this.setDepthTexture(this.depthTexture);
		} else {
		  this.ssaoMaterial.normalDepthBuffer = this.depthDownsamplingPass.texture;
		}

		/**
		 * A render target for the scene normals.
		 *
		 * @type {WebGLRenderTarget}
		 * @readonly
		 */

		this.renderTarget = renderTarget;

		if(this.renderTarget === undefined) {

			this.renderTarget = new WebGLRenderTarget(1, 1, {
				depthBuffer: false
			});

			this.renderTarget.texture.name = "AO.Target";

		}

		/**
		 * The resolution.
		 *
		 * @type {Resolution}
		 * @readonly
		 */

		const resolution = this.resolution = new Resolution(this, resolutionX, resolutionY, resolutionScale);
		resolution.addEventListener("change", (e) => this.setSize(resolution.baseWidth, resolution.baseHeight));

	}

	get ssaoMaterial() {
		return this.shaderPass.fullscreenMaterial;
	}

	/**
	 * Sets the normal buffer.
	 *
	 * @type {Texture}
	 */
	get normalBuffer() {
		return this.ssaoMaterial.normalBuffer;
	}
	set normalBuffer(value) {
		this.ssaoMaterial.normalBuffer = value;
		this.depthDownsamplingPass.fullscreenMaterial.normalBuffer = value;
	}
	

	/**
	 * The SSAO texture.
	 *
	 * @type {Texture}
	 */

	get texture() {

		return this.renderTarget.texture;

	}

	/**
	 * The SSAO texture.
	 *
	 * @deprecated Use texture instead.
	 * @return {Texture} The texture.
	 */

	getTexture() {

		return this.renderTarget.texture;

	}

	/**
	 * Returns the resolution settings.
	 *
	 * @deprecated Use resolution instead.
	 * @return {Resolution} The resolution.
	 */

	getResolution() {

		return this.resolution;

	}

	/**
	 * Returns the current resolution scale.
	 *
	 * @return {Number} The resolution scale.
	 * @deprecated Use resolution.preferredWidth or resolution.preferredHeight instead.
	 */

	getResolutionScale() {

		return this.resolution.scale;

	}

	/**
	 * Sets the resolution scale.
	 *
	 * @param {Number} scale - The new resolution scale.
	 * @deprecated Use resolution.preferredWidth or resolution.preferredHeight instead.
	 */

	setResolutionScale(scale) {

		this.resolution.scale = scale;

	}

	/**
	 * Sets the depth texture.
	 *
	 * @param {Texture} depthTexture - A depth texture.
	 * @param {DepthPackingStrategies} [depthPacking=BasicDepthPacking] - The depth packing.
	 */
	setDepthTexture(depthTexture, depthPacking = THREE.BasicDepthPacking) {
		// If this class has provided depth texture, then use it, else use depthTexture received from effectComposer 
		const currentDepthTexture = this.depthTexture !== null ? this.depthTexture : depthTexture;
		this.depthDownsamplingPass.setDepthTexture(currentDepthTexture, depthPacking);
		this.ssaoMaterial.depthBuffer = currentDepthTexture;
		this.ssaoMaterial.depthPacking = depthPacking;
	}
	

	/**
	 * Renders the ambient occlusion map from NormalDepth texture.
	 *
	 * @param {WebGLRenderer} renderer - The renderer.
	 * @param {WebGLRenderTarget} inputBuffer - A frame buffer that contains the result of the previous pass.
	 * @param {WebGLRenderTarget} outputBuffer - A frame buffer that serves as the output render target unless this pass renders to screen.
	 * @param {Number} [deltaTime] - The time between the last frame and the current one in seconds.
	 * @param {Boolean} [stencilTest] - Indicates whether a stencil mask is active.
	 */

	render(renderer, inputBuffer, outputBuffer, deltaTime, stencilTest) {
		if (this.depthDownsamplingPass.enabled) {
			this.depthDownsamplingPass.render(renderer);
		  }
		const renderTarget = this.renderToScreen ? null : this.renderTarget;
		this.shaderPass.render(renderer, renderTarget, renderTarget);

	}

	/**
	 * Updates the size of this pass.
	 *
	 * @param {Number} width - The width.
	 * @param {Number} height - The height.
	 */

	setSize(width, height) {

		const resolution = this.resolution;
		resolution.setBaseSize(width, height);
		this.renderTarget.setSize(resolution.width, resolution.height);

		this.depthDownsamplingPass.resolution.scale = resolution.scale;
		this.depthDownsamplingPass.setSize(width, height);	

		this.shaderPass.setSize(resolution.width, resolution.height);
		this.ssaoMaterial.copyCameraSettings(this.camera);
		this.ssaoMaterial.setSize(resolution.width, resolution.height);
	}

}

export class CustomDepthPass extends Pass {
	/**
	 * Constructs a new depth pass.
	 *
	 * @param {Scene} scene - The scene to render.
	 * @param {Camera} camera - The camera to use to render the scene.
	 * @param {Object} [options] - The options.
	 * @param {WebGLRenderTarget} [options.renderTarget] - A custom render target.
	 * @param {Number} [options.resolutionScale=1.0] - The resolution scale.
	 * @param {Number} [options.resolutionX=Resolution.AUTO_SIZE] - The horizontal resolution.
	 * @param {Number} [options.resolutionY=Resolution.AUTO_SIZE] - The vertical resolution.
	 * @param {Number} [options.width=Resolution.AUTO_SIZE] - Deprecated. Use resolutionX instead.
	 * @param {Number} [options.height=Resolution.AUTO_SIZE] - Deprecated. Use resolutionY instead.
	 */
	constructor(scene, camera, {
	  renderTarget,
	  resolutionScale = 1,
	  width = Resolution.AUTO_SIZE,
	  height = Resolution.AUTO_SIZE,
	  resolutionX = width,
	  resolutionY = height
	} = {}) {
	  super("CustomDepthPass");
	  this.needsSwap = false;
	  this.renderPass = new RenderPass(scene, camera);
	  //   this.renderPass = new RenderPass(scene, camera, new THREE.MeshDepthMaterial({
	// 	depthPacking: THREE.RGBADepthPacking
	//   }));
	  const renderPass = this.renderPass;
	  renderPass.skipShadowMapUpdate = true;
	  renderPass.ignoreBackground = true;
	  const clearPass = renderPass.clearPass;
	  clearPass.overrideClearColor = new THREE.Color(16777215);
	  clearPass.overrideClearAlpha = 1;
	  this.renderTarget = renderTarget;
	  if (this.renderTarget === void 0) {
		this.renderTarget = new THREE.WebGLRenderTarget(1, 1, {
		  minFilter: THREE.NearestFilter,
		  magFilter: THREE.NearestFilter,
		  depthBuffer: true,
		  depthTexture: new DepthTexture(1, 1)
		});
		this.renderTarget.texture.name = "DepthPass.Target";
	  }
	  const resolution = this.resolution = new Resolution(this, resolutionX, resolutionY, resolutionScale);
	  resolution.addEventListener("change", (e) => this.setSize(resolution.baseWidth, resolution.baseHeight));
	}
	set mainScene(value) {
	  this.renderPass.mainScene = value;
	}
	set mainCamera(value) {
	  this.renderPass.mainCamera = value;
	}
	/**
	 * The depth texture.
	 *
	 * @type {Texture}
	 */
	get texture() {
	  return this.renderTarget.texture;
	}
	/**
	 * Returns the depth texture.
	 *
	 * @deprecated Use texture instead.
	 * @return {Texture} The texture.
	 */
	getTexture() {
	  return this.renderTarget.texture;
	}
	/**
	 * Returns the resolution settings.
	 *
	 * @deprecated Use resolution instead.
	 * @return {Resolution} The resolution.
	 */
	getResolution() {
	  return this.resolution;
	}
	/**
	 * Returns the current resolution scale.
	 *
	 * @return {Number} The resolution scale.
	 * @deprecated Use resolution instead.
	 */
	getResolutionScale() {
	  return this.resolution.scale;
	}
	/**
	 * Sets the resolution scale.
	 *
	 * @param {Number} scale - The new resolution scale.
	 * @deprecated Use resolution instead.
	 */
	setResolutionScale(scale) {
		console.log("Depth setScale", scale);
	  this.resolution.scale = scale;
	}
	/**
	 * Renders the scene depth.
	 *
	 * @param {WebGLRenderer} renderer - The renderer.
	 * @param {WebGLRenderTarget} inputBuffer - A frame buffer that contains the result of the previous pass.
	 * @param {WebGLRenderTarget} outputBuffer - A frame buffer that serves as the output render target unless this pass renders to screen.
	 * @param {Number} [deltaTime] - The time between the last frame and the current one in seconds.
	 * @param {Boolean} [stencilTest] - Indicates whether a stencil mask is active.
	 */
	render(renderer, inputBuffer, outputBuffer, deltaTime, stencilTest) {
	  const renderTarget = this.renderToScreen ? null : this.renderTarget;
	  this.renderPass.scene.traverseVisible((child) => {
		if (child.isMesh && child instanceof CustomMesh) {
			// Switch material before rendering
			child._CustomDepthBackupMaterial = child.material;
			child.material = child.customDepthMaterial;
		}
	  });	  
	  this.renderPass.render(renderer, renderTarget);
	  this.renderPass.scene.traverseVisible((child) => {
		if (child.isMesh && child instanceof CustomMesh) {
			// Switch material before rendering
			child.material = child._CustomDepthBackupMaterial;
		}
	  });	  

	}
	/**
	 * Updates the size of this pass.
	 *
	 * @param {Number} width - The width.
	 * @param {Number} height - The height.
	 */
	setSize(width, height) {
		console.log("Depth setSize", width, height);
	  const resolution = this.resolution;
	  resolution.setBaseSize(width, height);
	  this.renderTarget.setSize(resolution.width, resolution.height);
	}
  };

  export class CustomRenderPass extends Pass {

	/**
	 * Constructs a new render pass.
	 *
	 * @param {Scene} scene - The scene to render.
	 * @param {Camera} camera - The camera to use to render the scene.
	 * @param {Object} [options] - The options.
	 * @param {WebGLRenderTarget} [options.renderTarget] - A custom render target.
	 * @param {Number} [options.resolutionScale=1.0] - The resolution scale.
	 * @param {Number} [options.resolutionX=Resolution.AUTO_SIZE] - The horizontal resolution.
	 * @param {Number} [options.resolutionY=Resolution.AUTO_SIZE] - The vertical resolution.
	 * @param {Number} [options.width=Resolution.AUTO_SIZE] - Deprecated. Use resolutionX instead.
	 * @param {Number} [options.height=Resolution.AUTO_SIZE] - Deprecated. Use resolutionY instead.
	 */

	constructor(scene, camera, depthTexture, shadowMapTexture, {
		renderTarget,
		resolutionScale = 1.0,
		width = Resolution.AUTO_SIZE,
		height = Resolution.AUTO_SIZE,
		resolutionX = width,
		resolutionY = height
	} = {}) {
		super("RenderPass");

		this.needsSwap = false;
		// this.renderToScreen = true;

		/**
		 * A render pass.
		 *
		 * @type {RenderPass}
		 * @private
		 */

		// NOTE: we can pass parameters such as uniforms, defines into this material
		// TODO: define necessary field for individual mesh material
		// RenderPass only use OverrideMaterialManager to change every mesh to render using the overridedMaterial
		// See OverrideMaterialManager's replaceMaterial in R3F's index.cjs
		// TODO: Before rendering, we should use the similar trick used by the OverrideMaterialManager
		// That is changing scene.overrideMaterial before renderer.render() to manually override each mesh's material
		// A little bit like SSRPass.js mode, but my implementation will be constructing a characteristic structure containing mesh specific defines and attribute and traverse each node to render the same material with mesh-specific characteristic
		// https://github.com/pmndrs/three-stdlib/blob/8e15117f8252a77cf1c0b12ce854919182f7c5ff/src/webxr/XRControllerModelFactory.ts#L10
		// this.renderPass = new RenderPass(scene, camera, new CustomMeshNormalMaterial({
		// 	defines: {
		// 		TEST_DEFINE: 1
		// 	}
		// })); // MeshNormalMaterial
		this.renderPass = new RenderPass(scene, camera);
		this.copyPass = new CopyPass();

		const renderPass = this.renderPass;
		renderPass.ignoreBackground = true;
		renderPass.skipShadowMapUpdate = true;

		const clearPass = renderPass.getClearPass();
		clearPass.overrideClearColor = new Color(0x000000);
		clearPass.overrideClearAlpha = 1.0;

		/**
		 * A render target for the scene normals.
		 *
		 * @type {WebGLRenderTarget}
		 * @readonly
		 */

		this.renderTarget = renderTarget;

		if(this.renderTarget === undefined) {

			this.renderTarget = new WebGLRenderTarget(1, 1, {
				minFilter: LinearFilter,
				magFilter: LinearFilter,
				depthBuffer: true,
				// depthTexture: new DepthTexture(1, 1),
				colorSpace: THREE.NoColorSpace,
			});

			this.renderTarget.texture.name = "CustomRenderPass.Target";

		}

		/**
		 * The resolution.
		 *
		 * @type {Resolution}
		 * @readonly
		 */

		const resolution = this.resolution = new Resolution(this, resolutionX, resolutionY, resolutionScale);
		resolution.addEventListener("change", (e) => this.setSize(resolution.baseWidth, resolution.baseHeight));

		this.depthTexture = depthTexture;
		this.shadowMapTexture = shadowMapTexture;
	}

	set mainScene(value) {

		this.renderPass.mainScene = value;

	}

	set mainCamera(value) {

		this.renderPass.mainCamera = value;

	}

	/**
	 * The normal texture.
	 *
	 * @type {Texture}
	 */

	get texture() {

		return this.renderTarget.texture;

	}

	/**
	 * The normal texture.
	 *
	 * @deprecated Use texture instead.
	 * @return {Texture} The texture.
	 */

	getTexture() {

		return this.renderTarget.texture;

	}

	/**
	 * Returns the resolution settings.
	 *
	 * @deprecated Use resolution instead.
	 * @return {Resolution} The resolution.
	 */

	getResolution() {

		return this.resolution;

	}

	/**
	 * Returns the current resolution scale.
	 *
	 * @return {Number} The resolution scale.
	 * @deprecated Use resolution.preferredWidth or resolution.preferredHeight instead.
	 */

	getResolutionScale() {

		return this.resolution.scale;

	}

	/**
	 * Sets the resolution scale.
	 *
	 * @param {Number} scale - The new resolution scale.
	 * @deprecated Use resolution.preferredWidth or resolution.preferredHeight instead.
	 */

	setResolutionScale(scale) {

		this.resolution.scale = scale;

	}

	/**
	 * Renders the scene normals.
	 *
	 * @param {WebGLRenderer} renderer - The renderer.
	 * @param {WebGLRenderTarget} inputBuffer - A frame buffer that contains the result of the previous pass.
	 * @param {WebGLRenderTarget} outputBuffer - A frame buffer that serves as the output render target unless this pass renders to screen.
	 * @param {Number} [deltaTime] - The time between the last frame and the current one in seconds.
	 * @param {Boolean} [stencilTest] - Indicates whether a stencil mask is active.
	 */

	render(renderer, inputBuffer, outputBuffer, deltaTime, stencilTest) {
		const renderTarget = this.renderToScreen ? null : this.renderTarget;

		// Render (Normal)
		this.renderPass.scene.traverseVisible((child) => {
			if (child.isMesh && child instanceof CustomMesh) {
				// Switch material before rendering
				child._CustomRenderBackupMaterial = child.material;
				child.material = child.customRenderMaterial;
				// console.log(child.material.uniforms);
				child.material.uniforms['depthMap'].value = this.depthTexture;
				child.material.uniforms['shadowMap'].value = this.shadowMapTexture;
			}
		  });	  
		renderer.shadowMap.enabled = true;
		// console.log(renderer, renderer.gammaInput);
		// this.renderPass.render(renderer, renderTarget, renderTarget);// FIXME original is this

		// FIXME below loc is for outputing to default frame buffer (SRGB format)
		renderer.outputColorSpace = THREE.LinearSRGBColorSpace;
		this.renderPass.renderToScreen = false;
		this.renderPass.render(renderer, inputBuffer, this.renderTarget);
		// FIXME above loc is for outputing to default frame buffer (SRGB format)

		// this.renderPass.render(renderer, inputBuffer, renderTarget); // FIXME original is this, if you want to debug shader in normally then uncomment this and comment other sRGB code (Linear format)
		// console.log(this.renderPass);

		// Render (Outline)
		// console.log(inputBuffer.textures[0], this.renderTarget.textures[0]);
		this.renderPass.clearPass.enabled = false;
		this.renderPass.scene.traverseVisible((child) => {
		if (child.isMesh && child instanceof CustomMesh) {
			// Switch material before rendering
			child._CustomRenderBackupMaterial = child.material;
			child.material = child.customRenderOutlineMaterial;
			// console.log(child.material.uniforms);
			child.material.uniforms['depthMap'].value = this.depthTexture;
			child.material.uniforms['shadowMap'].value = this.shadowMapTexture;
		}
		});	  
		// this.renderPass.render(renderer, inputBuffer, renderTarget);
		// this.renderPass.render(renderer, renderTarget, renderTarget); // FIXME original is this, if you want to debug shader in normally then uncomment this and comment other sRGB code (Linear format)
		// FIXME below loc is for outputing to default frame buffer (SRGB format)
		this.renderPass.render(renderer, inputBuffer, this.renderTarget);
		// FIXME above loc is for outputing to default frame buffer (SRGB format)
		this.renderPass.scene.traverseVisible((child) => {
			if (child.isMesh) {
				child.material = child._CustomRenderBackupMaterial;
			}
		  });
		this.renderPass.clearPass.enabled = true;
		renderer.shadowMap.enabled = false;
		renderer.outputColorSpace = THREE.SRGBColorSpace; // FIXME should adopt this
		// // FIXME below loc is for outputing to default frame buffer (SRGB format)
		// this.copyPass.renderToScreen = true;
		// this.copyPass.render(renderer, this.renderTarget, inputBuffer); // FIXME should be this, but currently facing color problem
		// // FIXME above loc is for outputing to default frame buffer (SRGB format)

	}

	/**
	 * Updates the size of this pass.
	 *
	 * @param {Number} width - The width.
	 * @param {Number} height - The height.
	 */

	setSize(width, height) {

		const resolution = this.resolution;
		resolution.setBaseSize(width, height);
		this.renderTarget.setSize(resolution.width, resolution.height);

	}

}

